import React from 'react';
import  Modal from 'react-modal';

const OptionModal=(props)=>(
    <Modal
        isOpen={!!props.selectedOption}
        onRequestClose={props.handleClearSelectedOption}
        contentLabel="Selected Option"
        closeTimeoutMS={200}
        className="modal"
        ariaHideApp={false}
        >
        <h3 className="modal__title">{props.getString('modalTitle')}</h3>
        {props.selectedOption&& <p className="model__body">{props.selectedOption}</p>}
        <button className="button" onClick={props.handleClearSelectedOption}>
            {props.getString('modalButton')}</button>
    </Modal>
);

export default OptionModal;
