import React from 'react';

import AddOption from './AddOption';
import Action from "./Action";
import Header from "./Header";
import {getLangString} from "../utils/language.js";
import Options from "./Options";
import OptionModal from './OptionModal';

export default class RandomTodos extends React.Component {
    state = {
        options: [],
        selectedOption: undefined,
        lang: 'english',
        errorType: undefined
    };
/*************************Event Handlers***************************************/

    handleDeleteOptions = () => this.setState(() => ({options: []}));

    handleDeleteOption = (optionToRemove) => this.setState((prevState) => ({
        options: prevState.options.filter((option) => optionToRemove !== option)
    }));

    handlePick = () => {
        const randomNum = Math.floor(Math.random() * this.state.options.length);
        const option = this.state.options[randomNum];
        this.setState(() => ({selectedOption: option}));
    };

    handleClearSelectedOption = () => this.setState(() => ({selectedOption: undefined}));

    handleAddOption = (option) => {
        if (!option) {
            this.setState(() => ({errorType: 'notValidError'}));
            return this.getString('notValidError');
        } else if (this.state.options.indexOf(option) > -1) {
            this.setState(() => ({errorType: 'duplicateError'}));
            return this.getString('duplicateError');
        }

        this.setState((prevState) => ({
            options: prevState.options.concat(option)
        }));
    };

    handleChangeLanguage = (lang) => {
        this.setState(() => ({lang: lang}));
    };

    getString = (section) => getLangString(this.state.lang, section);

    componentDidMount() {
        try {
            let json = localStorage.getItem('options');
            const options = JSON.parse(json);

            json = localStorage.getItem('lang');
            const lang = JSON.parse(json);

            //store options in local storage
            if (options) {
                this.setState(() => ({options: options}));
            }
            if (lang) {
                this.setState(() => ({lang: lang}));
            }
        } catch (e) {
            //do nothing
        }

    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.options.length !== this.state.options.length) {
            const json = JSON.stringify(this.state.options);
            localStorage.setItem('options', json);
        }
        if (prevState.lang !== this.state.lang) {
            const json = JSON.stringify(this.state.lang);
            localStorage.setItem('lang', json);
        }
        console.log('componentDidUpdate');
    }

    componentWillUnmount() {
        console.log('componentWillUnmount');
    }

    render() {
        return (
            <div className="body">
                <Header getString={this.getString}
                        handleChangeLanguage={this.handleChangeLanguage}
                />

                <div className="container">
                    <div className="widget">
                        <Options
                            options={this.state.options}
                            getString={this.getString}
                            handleDeleteOptions={this.handleDeleteOptions}
                            handleDeleteOption={this.handleDeleteOption}
                        />
                        <AddOption
                            handleAddOption={this.handleAddOption}
                            getString={this.getString}
                            updateErrorLang={this.updateErrorLang}
                        />
                    </div>

                    <Action
                        hasOptions={this.state.options.length > 0}
                        handlePick={this.handlePick}
                        getString={this.getString}
                    />

                    <OptionModal
                        selectedOption={this.state.selectedOption}
                        handleClearSelectedOption={this.handleClearSelectedOption}
                        getString={this.getString}
                    />
                </div>
            </div>
        );
    }
}
