import React from 'react';

const Header=(props)=>{
    const handleChangeLanguage=(e)=>{
        const selectedLang=e.target.value;

        if(selectedLang!=='default') {
            props.handleChangeLanguage(selectedLang);
        }
    };
    return(
        <div className="header">
            <div className="container">
                <h1 className="header__title">{props.getString('headerTitle')}</h1>

                <div className="header__subtitle__select">
                <h2 className="header__subtitle">{props.getString( 'headerSubtitle')}</h2>


            <select className="header__selection"
                    defaultValue="default"
                    onChange={handleChangeLanguage}>
                    <option value="default">{props.getString('defaultLang')}</option>
                    <option value="english">{props.getString('english')}</option>
                    <option value="japanese">{props.getString('japanese')}</option>
                </select>
                </div>
            </div>
        </div>
    );
};

Header.defaultProps={
    title: 'TodoList'
};

export default Header;